import { Container } from "react-bootstrap";
import "./App.css";
import HomePage from "./components/HomePage";
import styles from "./App.module.css";
import { useState, useEffect, lazy, Suspense } from "react";
import { useTheme } from "./ThemeContext";
import NavBarModule from "./NavBarModule";
import FooterModule from "./FooterModule";
import { Routes, Route, useNavigate } from "react-router-dom";
import { useLocation } from "react-router-dom";
import NotFoundPage from "./components/NotFoundPage";
import CarouselPage from "./components/CarouselPage";
import { useAuth } from "./AuthContext";
import ProtectedRoute from "./ProtectedRoute";

const LoginPage = lazy(() => import("./components/LoginPage"));
const DataCenterPage = lazy(() => import("./components/DataCenterPage"));
const DedicatedServersPage = lazy(() =>
  import("./components/DedicatedServersPage")
);
const VirtualServersPage = lazy(() =>
  import("./components/VirtualServersPage")
);
const ResourcesPage = lazy(() => import("./components/ResourcesPage"));
const PrivacyPage = lazy(() => import("./components/PrivacyPage"));
const TermsPage = lazy(() => import("./components/TermsPage"));
const ReferralPage = lazy(() => import("./components/ReferralPage"));
const VirtualHostingPage = lazy(() =>
  import("./components/VirtualHostingPage")
);

function useWindowWidth() {
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWidth(window.innerWidth);

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return {
    isXs: width < 576,
    isSm: width >= 576 && width < 768,
    isMd: width >= 768 && width < 992,
    isLg: width >= 992 && width < 1200,
    isXl: width >= 1200,
  };
}

function useYandexMetrika() {
  const location = useLocation();
  useEffect(() => {
    if (window.ym) {
      window.ym(95886642, "hit", location.pathname + location.search);
    }
  }, [location]);
}

