//   home: HomePage,
//   dataCenter: DataCenterPage,
//   virtualServers: VirtualServersPage,
//   dedicatedServers: DedicatedServersPage,
//   resources: ResourcesPage,
// };

const pageMapping = {
  home: "",
  dataCenter: "data-center",
  virtualServers: "servers",
  dedicatedServers: "dedicated",
  resources: "brandbook",
  privacy: "privacy",
  terms: "terms",
  referral: "referral",
  virtualHosting: "virtual-hosting",
  carousel: "carousel",
  login: "login",
};

function App() {
  const [isNavOpened, setIsNavOpened] = useState(false);
  const windowSize = useWindowWidth();
  const { s, toggleTheme } = useTheme();
  const navigate = useNavigate();

  const location = useLocation();
  const hideNavOnRoutes = ["/login"];
  const shouldShowNav = !hideNavOnRoutes.includes(location.pathname);

  const hideFooterOnRoutes = ["/login", "/carousel"];
  const shouldShowFooter = !hideFooterOnRoutes.includes(location.pathname);

  const setCurPage = (page) => {
    console.log(page);
    navigate(`/${pageMapping[page]}`);
  };

  useYandexMetrika();
  return (
    <div className="App" style={s.app}>
      <Container fluid>
        <div className={`${styles.landingContainer}`}>
          {shouldShowNav && (
            <div className="pt-3">
              <NavBarModule
                setIsOpened={setIsNavOpened}
                isOpened={isNavOpened}
                {...windowSize}
                setCurPage={setCurPage}
              />
            </div>
          )}
          <Routes>
            <Route
              path="/"
              element={
                <Suspense fallback={<div>...</div>}>
                  <HomePage windowSize={windowSize} setCurPage={setCurPage} />
                </Suspense>
              }
            />
            <Route
              path="/data-center"
              element={
                <Suspense fallback={<div>...</div>}>
                  <DataCenterPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/servers"
              element={
                <Suspense fallback={<div>...</div>}>
                  <VirtualServersPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/dedicated"
              element={
                <Suspense fallback={<div>...</div>}>
                  <DedicatedServersPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/brandbook"
              element={
                <Suspense fallback={<div>...</div>}>
                  <ResourcesPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/privacy"
              element={
                <Suspense fallback={<div>...</div>}>
                  <PrivacyPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/terms"
              element={
                <Suspense fallback={<div>...</div>}>
                  <TermsPage windowSize={windowSize} setCurPage={setCurPage} />
                </Suspense>
              }
            />
            <Route
              path="/referral"
              element={
                <Suspense fallback={<div>...</div>}>
                  <ReferralPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/virtual-hosting"
              element={
                <Suspense fallback={<div>...</div>}>
                  <VirtualHostingPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
            <Route
              path="/carousel"
              element={
                <Suspense fallback={<div>...</div>}>
                  <ProtectedRoute>
                    <CarouselPage
                      windowSize={windowSize}
                      setCurPage={setCurPage}
                    />
                  </ProtectedRoute>
                </Suspense>
              }
            />
            <Route
              path="/login"
              element={
                <Suspense fallback={<div>...</div>}>
                  <LoginPage windowSize={windowSize} setCurPage={setCurPage} />
                </Suspense>
              }
            />
            <Route
              path="*"
              element={
                <Suspense fallback={<div>...</div>}>
                  <NotFoundPage
                    windowSize={windowSize}
                    setCurPage={setCurPage}
                  />
                </Suspense>
              }
            />
          </Routes>
          {/* <Page windowSize={windowSize} setCurPage={setCurPage} /> */}
          {/* <HomePage windowSize={windowSize} /> */}
          {shouldShowFooter && (
            <FooterModule {...windowSize} setCurPage={setCurPage} />
          )}
        </div>
      </Container>
    </div>
  );
}

export default App;
