  //     const targetX = -((newOffset - 0.5) * itemWidth);
  //     await controls.start({
  //       x: targetX,
  //       transition: { duration: 2.5, ease: [0.1, 0.6, 0.1, 1] },
  //     });

  //     //  
  //     setIsOverlay(items[winnerIndex]);

  //     //  offset,   
  //     const finalOffset =
  //       ((winnerIndex - visibleCenterOffset + items.length) % items.length) + 3;
  //     const finalX = -((finalOffset - 0.5) * itemWidth);

