import { Row, Col, Navbar, Nav } from "react-bootstrap";
import "./App.css";
import styles from "./App.module.css";
import { useState, useEffect, useRef } from "react";
import { useTheme } from "./ThemeContext";
import { useLang } from "./LangContext";
import { useAuth } from "./AuthContext";

function BurgerButton({ setMobMenu, setMobServices }) {
  const [isActive, setIsActive] = useState(false);
  const { s, toggleTheme } = useTheme();

  const toggleBurger = () => {
    setIsActive((prev) => !prev);
  };

  return (
    <button
      className={styles.menuBtn}
      onClick={() => {
        setMobMenu((state) => !state);
        setMobServices(false);
        toggleBurger();
      }}
    >
      <div className={`${styles.burger} ${isActive ? styles.active : ""}`}>
        <span style={{ backgroundColor: s.navIconsColor }}></span>
        <span style={{ backgroundColor: s.navIconsColor }}></span>
        <span style={{ backgroundColor: s.navIconsColor }}></span>
      </div>
    </button>
  );
}

function NavBarMenu({ setCurPage }) {
  const { lang, changeLang, t } = useLang();
  const { s, toggleTheme } = useTheme();

  return (
    <div className={styles.navMenu} style={s.navBar}>
      <Row xs={3} xl={6} className="px-4">
        <Col>
          <Row xs={1} className="gy-2">
            <Col className={styles.navHeader}>
              {/* <img src="/img/serversIconDark.png" alt="IMG" /> */}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
              >
                <path
                  d="M5 14H19V4H5V14ZM5 16V20H19V16H5ZM4 2H20C20.5523 2 21 2.44772 21 3V21C21 21.5523 20.5523 22 20 22H4C3.44772 22 3 21.5523 3 21V3C3 2.44772 3.44772 2 4 2ZM15 17H17V19H15V17Z"
                  fill={s.navIconsColor}
                />
              </svg>
              <div className="ms-2" style={s.navMenuHeaders}>
                {t("navHeader1")}
              </div>
            </Col>
            <Col>
              <span
                className={styles.footerLink}
                style={s.navHeaders}
                onClick={() => setCurPage("virtualServers")}
              >
                {t("navHeader1_1")}
              </span>
            </Col>
            <Col>
              <span
                className={styles.footerLink}
                style={s.navHeaders}
                onClick={() => setCurPage("dedicatedServers")}
              >
                {t("navHeader1_2")}
              </span>
            </Col>
          </Row>
        </Col>
        <Col>
          <Row xs={1} className="gy-2">
            <Col className={styles.navHeader}>
              {/* <img src="/img/hostingIconDark.png" alt="IMG" /> */}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
              >
                <path
                  d="M5 11H19V5H5V11ZM21 4V20C21 20.5523 20.5523 21 20 21H4C3.44772 21 3 20.5523 3 20V4C3 3.44772 3.44772 3 4 3H20C20.5523 3 21 3.44772 21 4ZM19 13H5V19H19V13ZM7 15H10V17H7V15ZM7 7H10V9H7V7Z"
                  fill={s.navIconsColor}
                />
              </svg>
              <div className="ms-2" style={s.navMenuHeaders}>
                {t("navHeader2")}
              </div>
            </Col>
            <Col>
              <span
                className={styles.footerLink}
                style={s.navHeaders}
                onClick={() => setCurPage("virtualHosting")}
              >
                {t("navHeader2_1")}
              </span>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

