import { Row, Col, Collapse } from "react-bootstrap";
import { useState, useRef, useEffect } from "react";
import styles from "./HomePage.module.css";
import "./HomePage.css";
import { useLang } from "../LangContext";
import { useTheme } from "../ThemeContext";
import { QuestionBlock, QuestionCard } from "./QuestionsModule";
import MinorHeader from "./MinorHeader";
import fireIMG from "../assets/img/fire.webp";
import rocketIMG from "../assets/img/rocket.webp";
import programmerIMG from "../assets/img/programmer.webp";
import phoneIMG from "../assets/img/phone.webp";
import keyIMG from "../assets/img/key.webp";
import graphIMG from "../assets/img/graph.webp";

import balanceMP4 from "../assets/img/balance.MP4";
import serverMP4 from "../assets/img/server.MP4";
import startMP4 from "../assets/img/start.MP4";

function ProsSmallCard({ header, description, gifSrc, squareText }) {
  const descriptionClass = `mt-1 mt-lg-2`;
  const divClass = `${styles.card} `;
  const { s, toggleTheme } = useTheme();

  return (
    <Col>
      <div
        className={`${divClass} ${styles.smallCardStructure} `}
        style={s.cardBg}
      >
        <Row className="g-0">
          <Col
            xs={12}
            md={8}
            // style={{ paddingTop: "20px", paddingLeft: "33px" }}
            className={`d-flex flex-column justify-content-center`}
            // className="d-flex flex-column justify-content-center pt-3 pt-lg-4 ps-4 ps-lg-5"
          >
            <h4
              className={styles.cardHeader}
              style={{ ...s.cardHeader, fontFamily: "Gilroy-semibold" }}
            >
              {header}{" "}
              <span style={{ backgroundColor: s.squareTextBg }}>
                {squareText}
              </span>
            </h4>
            <p className={descriptionClass} style={s.cardDescr}>
              {description}
            </p>
          </Col>
          <Col
            xs={12}
            md={4}
            className="d-flex align-items-center justify-content-center"
          >
            <img
              draggable="false"
              src={gifSrc}
              alt="GIF"
              className={styles.prosSmallGif}
            />
          </Col>
        </Row>
      </div>
    </Col>
  );
}
function ProsBigCard({ header, description, gifSrc1, gifSrc2, style }) {
  const { s, toggleTheme } = useTheme();

  const divClass = `${styles.card}`;
  const headingPart = (
    <Col
      xs={12}
      //   style={{
      //     paddingTop: "20px",
      //     paddingLeft: "33px",
      //     paddingRight: "20px",
      //   }}
      // className="pt-3 pt-lg-4 ps-4 ps-lg-5 pe-4 pe-lg-5 "
    >
      <h4 style={{ ...s.cardHeader, fontFamily: "Gilroy-semibold" }}>
        {header}
      </h4>
      <p style={s.cardDescr}>{description}</p>
    </Col>
  );
  const gifPart = (
    <Col xs={12}>
      <Row className="g-0">
        <Col
          className="d-flex align-items-center justify-content-center"
          xs={5}
        >
          <img
            draggable="false"
            src={gifSrc1}
            alt="GIF"
            className={styles.prosSmallGif}
          />
        </Col>
        <Col
          className="d-flex align-items-center justify-content-center"
          xs={2}
        >
          {/* <img
            src={"/img/cardArrowsDark.png"}
            alt="IMG"
            className={styles.arrowsImg}
          /> */}
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="52"
            height="52"
            viewBox="0 0 52 52"
            fill="none"
            className={styles.arrowsImg}
          >
            <rect
              x="0.5"
              y="0.5"
              width="51"
              height="51"
              rx="15.5"
              fill={s.prosBigArrowBg}
            />
            <rect
              x="0.5"
              y="0.5"
              width="51"
              height="51"
              rx="15.5"
              stroke={s.prosBigArrowStroke}
            />
            <path
              d="M32.2222 32.4V26L40 34L32.2222 42V35.6H13.5556V32.4H32.2222ZM19.7778 10V16.3984L38.4444 16.4V19.6H19.7778V26L12 18L19.7778 10Z"
              fill="url(#paint0_linear_3315_464)"
            />
            <defs>
              <linearGradient
                id="paint0_linear_3315_464"
                x1="12"
                y1="17.5"
                x2="40"
                y2="34"
                gradientUnits="userSpaceOnUse"
              >
                <stop stopColor="#C5C5C5" />
                <stop offset="1" stopColor="#949494" />
              </linearGradient>
            </defs>
          </svg>
        </Col>
        <Col
          className="d-flex align-items-center justify-content-center"
          xs={5}
        >
          <img
            draggable="false"
            src={gifSrc2}
            alt="GIF"
            className={styles.prosSmallGif}
          />
        </Col>
      </Row>
    </Col>
  );
  return (
    <Col>
      <div
        className={`${divClass} ${styles.prosBigCardStructure} h-100`}
        style={s.cardBg}
      >
        <Row className="g-0">
          {style === 1 ? (
            <>
              {headingPart}
              <div className="mt-3"></div>
              {gifPart}
            </>
          ) : (
            <>
              {gifPart}
              <div className="mt-3"></div>
              {headingPart}
            </>
          )}
        </Row>
      </div>
    </Col>
  );
}

function ProsModule() {
  const { lang, changeLang, t } = useLang();
  return (
    <Row xs={1} lg={2} className="g-4 mt-2 mt-lg-4 align-items-stretch">
      <ProsSmallCard
        header={t("prosCard1Header1")}
        description={t("prosCard1Descr1")}
        gifSrc={fireIMG}
      />
      <ProsSmallCard
        header={t("prosCard1Header2")}
        description={t("prosCard1Descr2")}
        gifSrc={rocketIMG}
        squareText={"100%"}
      />
      <ProsBigCard
        header={t("prosCard1Header3")}
        description={t("prosCard1Descr3")}
        gifSrc1={programmerIMG}
        gifSrc2={phoneIMG}
        style={1}
      />
      <ProsBigCard
        header={t("prosCard1Header4")}
        description={t("prosCard1Descr4")}
        gifSrc1={keyIMG}
        gifSrc2={graphIMG}
        style={2}
      />
    </Row>
  );
}

function Pros2Card({ header, description, picSrc, children }) {
  const { s, toggleTheme } = useTheme();

  const descriptionClass = `${styles.cardDescr}`;
  const headerClass = `${styles.cardHeader} m-0`;
  const divClass = `${styles.card} h-100 ${styles.pros2CardStructure}`;
  return (
    <Col>
      <div
        className={divClass}
        style={s.cardBg}
        //   style={{ paddingBottom: "7px" }}
      >
        <Row>
          <Col xs={12}>
            <div className="d-flex align-items-center gap-3">
              <div>
                <div className="panel-container" style={s.card2IconBG}>
                  {children}
                </div>
              </div>
              {/* <img src={picSrc} alt="IMG" className={styles.prosSmallGif} /> */}
              <h4
                className={headerClass}
                style={{
                  fontSize: "22px",
                  ...s.cardHeader,
                  fontFamily: "Gilroy-semibold",
                }}
              >
                {header}
              </h4>
            </div>
          </Col>
          <Col xs={12}>
            <p
              className={descriptionClass}
              style={{ marginTop: "28px", ...s.card2Descr }}
              // style={{ maxWidth: "82%" }}
            >
              {description}
            </p>
          </Col>
        </Row>
      </div>
    </Col>
  );
}

