function MainBanner({
  header1,
  header2,
  description1,
  description2,
  description3,
  btnText,
  isXs,
  isSm,
  isXl,
  isLg,
  setCurPage,
}) {
  const descriptionClass = `${styles.mainBannerDescr} mt-3 mt-lg-4`;
  const btnClass = `${styles.mainBannerBtn} mt-3 mt-lg-5`;

  return (
    //         relative positioning  overflow
    <div
      className={`${styles.mainBannerWrapper} d-flex flex-column justify-content-center align-items-center pb-3 pb-lg-5 pt-3 pt-lg-4`}
    >
      {/*    */}
      {(isLg || isXl) && (
        <>
          {/* <img
            src="/img/bannerArrowsLeft.png"
            alt="  "
            className={styles.leftDecor}
          /> */}
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="370"
            height="426"
            viewBox="0 0 370 426"
            fill="none"
            className={styles.leftDecor}
          >
            <path
              d="M314.998 47.122L-33.4931 -44M301.219 100.069L-45.8065 70.9941M296.806 156.132L-97 189.617M287.71 216.242L-89.1953 315.129M323.433 260.373L-65.0271 456.5M354.373 309.217L176.651 430.093M368.973 371.893L241.084 495.215"
              stroke="url(#paint0_linear_3315_437)"
              strokeWidth="2"
            />
            <defs>
              <linearGradient
                id="paint0_linear_3315_437"
                x1="49.3589"
                y1="189.79"
                x2="296.859"
                y2="170.29"
                gradientUnits="userSpaceOnUse"
              >
                <stop stopColor="white" stopOpacity="0.44" />
                <stop offset="1" stopColor="#ABBED3" stopOpacity="0" />
              </linearGradient>
            </defs>
          </svg>
          {/* <img
            src="/img/bannerArrowsRight.png"
            alt="  "
            className={styles.rightDecor}
          /> */}
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="375"
            height="426"
            viewBox="0 0 375 426"
            fill="none"
            className={styles.rightDecor}
          >
            <path
              d="M54.9749 47.122L403.466 -44M68.7538 100.069L415.779 70.9941M73.1669 156.132L466.973 189.617M82.263 216.242L459.168 315.129M46.5396 260.373L435 456.5M15.5997 309.217L193.322 430.093M1.00006 371.893L128.889 495.215"
              stroke="url(#paint0_linear_3315_436)"
              strokeWidth="2"
            />
            <defs>
              <linearGradient
                id="paint0_linear_3315_436"
                x1="320.614"
                y1="189.79"
                x2="73.1141"
                y2="170.29"
                gradientUnits="userSpaceOnUse"
              >
                <stop stopColor="white" stopOpacity="0.44" />
                <stop offset="1" stopColor="#B2C4D6" stopOpacity="0" />
              </linearGradient>
            </defs>
          </svg>
        </>
      )}

      <Row className="w-100">
        <Col>
          {isXs || isSm ? (
            <>
              <h4 className={`${styles.mainBannerHeader} mt-lg-3`}>
                {[header1, header2].join(" ")}
              </h4>
              <p className={descriptionClass}>
                {[description1, description2, description3].join(" ")}
              </p>
            </>
          ) : (
            <>
              <h4 className={`${styles.mainBannerHeader} mt-lg-3`}>
                {header1}
                <br />
                {header2}
              </h4>
              <p className={descriptionClass}>
                {description1}
                <br />
                {description2}
                <br />
                {description3}
              </p>
            </>
          )}
          <button
            className={btnClass}
            onClick={() => setCurPage("virtualServers")}
          >
            {btnText}
          </button>
        </Col>
      </Row>
    </div>
  );
}

