const carouselStyles = `
  .carousel-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding: 0 0.5px;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
  gap: 25px;
}

.carousel-slide {
  flex: 0 0 calc(100% / 3 - 25px * 2 / 3); /*     gap */
  box-sizing: border-box;
}
  
  @media (max-width: 992px) {
    .carousel-slide {
      flex: 0 0 100%;
    }
  }
  
  .carousel-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  z-index: 10;
  cursor: pointer;
  border: none;
  background: transparent;
  padding: 0;
}

/*       */
.carousel-arrow--left {
  left: -20px; /*    */
}

.carousel-arrow--right {
  right: -20px; /*    */
}
  `;
function FDCard({ header, description, picSrc }) {
  const { lang, changeLang, t } = useLang();
  const { s, toggleTheme } = useTheme();

  const descriptionClass = `${styles.cardDescr}`;
  const headerClass = `${styles.cardHeader} m-0`;
  const divClass = `${styles.card} h-100 ${styles.FDCardStructure}`;

  return (
    <div className={divClass} style={s.cardBg}>
      <Row className="d-flex align-items-center">
        <Col xs="auto">
          <div className="panel-container">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="28"
              height="28"
              viewBox="0 0 28 28"
              fill="none"
            >
              <mask
                id="mask0_3315_555"
                style={{ maskType: "luminance" }}
                maskUnits="userSpaceOnUse"
                x="0"
                y="0"
                width="28"
                height="26"
              >
                <path d="M28 0H0V25.4057H28V0Z" fill="white" />
              </mask>
              <g mask="url(#mask0_3315_555)">
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M7.18359 22.1346C9.7484 24.2181 12.7113 25.4055 15.8712 25.4055C20.5702 25.4055 24.8359 22.7719 28.0014 18.4822C26.6649 16.6716 25.1328 15.1549 23.453 14.0083L20.7559 15.3547C21.3356 16.2565 21.6732 17.33 21.6732 18.4822C21.6732 21.6829 19.0761 24.2772 15.8712 24.2772C13.381 24.2772 11.258 22.71 10.4349 20.5095L7.18359 22.1331V22.1346ZM13.8748 18.7931C13.9395 19.2138 14.1365 19.605 14.4418 19.9102C14.8202 20.2887 15.3351 20.5011 15.8712 20.4997C16.9868 20.4997 17.8915 19.5964 17.8915 18.4822C17.8915 17.9349 17.6734 17.4383 17.3189 17.0739L13.8762 18.7931H13.8748Z"
                  fill="#2BAD72"
                />
                <path
                  d="M7.91847 18.4374L27.5771 8.98725L23.2565 0L21.0251 7.94195L18.3436 2.36221L16.1108 10.3041L13.4293 4.72437L11.1966 12.6663L8.515 7.08652L6.28226 15.0285L3.60076 9.44873L0.000488281 22.2444L4.91478 19.8823L7.91847 18.4388V18.4374Z"
                  fill="#2BAD72"
                />
              </g>
            </svg>
          </div>
        </Col>
        <Col>
          <h4
            className={headerClass}
            style={{
              fontSize: "18px",
              ...s.cardHeader,
              fontFamily: "Gilroy-semibold",
            }}
          >
            {header}
          </h4>
          <p
            className="m-0"
            style={{ color: s.fdPlaceholder, fontSize: "16px" }}
          >
            {t("fdPlaceholder")}
            <a style={{ textDecoration: "none", color: "#23C1FF" }}>
              lolz.live
            </a>
          </p>
        </Col>
      </Row>
      <Row>
        <Col xs={12}>
          <p
            className={descriptionClass}
            style={{ color: s.fdDescr, marginTop: "29px" }}
          >
            {description}
          </p>
        </Col>
      </Row>
    </div>
  );
}

function FDModule() {
  const { lang, changeLang, t } = useLang();

  const cards = [
    {
      header: t("fdHeader1"),
      description: t("fdDescr1"),
      picSrc: "/img/lolzIcon.png",
    },
    {
      header: t("fdHeader2"),
      description: t("fdDescr2"),
      picSrc: "/img/lolzIcon.png",
    },
    {
      header: t("fdHeader3"),
      description: t("fdDescr3"),
      picSrc: "/img/lolzIcon.png",
    },
    {
      header: t("fdHeader4"),
      description: t("fdDescr4"),
      picSrc: "/img/lolzIcon.png",
    },
    {
      header: t("fdHeader5"),
      description: t("fdDescr5"),
      picSrc: "/img/lolzIcon.png",
    },
  ];

  const [currentIndex, setCurrentIndex] = useState(0);
  const isMobile = window.innerWidth <= 992; //   
  const visibleCards = isMobile ? 1 : 3; //   

  const nextSlide = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex + 1 <= cards.length - visibleCards ? prevIndex + 1 : 0
    );
  };

  const prevSlide = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex - 1 >= 0 ? prevIndex - 1 : cards.length - visibleCards
    );
  };

  const getTransformValue = () => {
    if (window.innerWidth <= 992) {
      return `translateX(calc(-${currentIndex} * (100% + 25px)))`; //    
    }
    return `translateX(calc(-${currentIndex} * (100% + 25px) / 3))`; //  
  };

  return (
    <>
      <style>{carouselStyles}</style>
      <div
        style={{ position: "relative", width: "100%" }}
        className="mt-4 mt-lg-5"
      >
        <div className="carousel-container">
          <div
            className="carousel-track"
            style={{
              transform: getTransformValue(),
            }}
          >
            {cards.map((card, index) => (
              <div key={index} className="carousel-slide">
                <FDCard {...card} />
              </div>
            ))}
          </div>
        </div>

        {/*     */}
        {currentIndex !== 0 && (
          <button
            className="carousel-arrow carousel-arrow--left"
            onClick={prevSlide}
          >
            <svg
              width="40"
              height="40"
              viewBox="0 0 40 40"
              fill="none"
              className={styles.arrowIconHover}
            >
              <circle cx="20" cy="20" r="20" fill="#232323" />
              <path
                d="M17.4374 20.0009L24.0371 13.4012L22.1515 11.5156L13.6662 20.0009L22.1515 28.4861L24.0371 26.6005L17.4374 20.0009Z"
                fill="#F8F8F8"
              />
            </svg>
          </button>
        )}
        {((isMobile && currentIndex !== 4) ||
          (!isMobile && currentIndex !== 2)) && (
          <button
            className="carousel-arrow carousel-arrow--right"
            onClick={nextSlide}
          >
            <svg
              width="40"
              height="40"
              viewBox="0 0 40 40"
              fill="none"
              className={styles.arrowIconHover}
            >
              <circle cx="20" cy="20" r="20" fill="#232323" />
              <path
                d="M22.5626 20.0009L15.9629 13.4012L17.8485 11.5156L26.3338 20.0009L17.8485 28.4861L15.9629 26.6005L22.5626 20.0009Z"
                fill="#F8F8F8"
              />
            </svg>
          </button>
        )}
      </div>
    </>
  );
}
function QuestionsModule() {
  const [openedCard, setOpenedCard] = useState(null);
  const { lang, changeLang, t } = useLang();

  const cards = [
    {
      header: t("qHeader1"),
      isOpened: true,
      description: t("qDescr1"),
      key: 0,
    },
    {
      header: t("qHeader2"),
      isOpened: true,
      description: t("qDescr2"),
      key: 1,
    },
    {
      header: t("qHeader3"),
      isOpened: true,
      description: t("qDescr3"),
      key: 2,
    },
    {
      header: t("qHeader4"),
      isOpened: true,
      description: t("qDescr4"),
      key: 3,
    },
  ];
  return (
    <Row className="mt-2  gx-3 gy-3">
      <Col xs={12} md={12} lg={6}>
        <Row xs={1} className="gy-2">
          {cards.map((el) => (
            <Col>
              <QuestionCard
                {...el}
                isOpened={el.key === openedCard}
                setOpened={setOpenedCard}
                index={el.key}
                key={el.key}
              />
            </Col>
          ))}
        </Row>
      </Col>
      <Col xs={12} md={6} lg={3}>
        <QuestionBlock
          header={t("qCardHeader1")}
          description={t("qCardDescr1")}
          linkText={t("qCardLink1")}
          link={"https://wiki.qwins.co/"}
        />
      </Col>
      <Col xs={12} md={6} lg={3}>
        <QuestionBlock
          header={t("qCardHeader2")}
          description={t("qCardDescr2")}
          linkText={t("qCardLink2")}
          link={"https://t.me/qwinshelp"}
        />
      </Col>
    </Row>
  );
}
export default function HomePage({ windowSize, isLightTheme, setCurPage }) {
  const { lang, changeLang, t } = useLang();
  const { s, toggleTheme } = useTheme();

  return (
    <>
      <MainBanner
        header1={t("mainBannerHeader1")}
        header2={t("mainBannerHeader2")}
        description1={t("mainBannerDescr1")}
        description2={t("mainBannerDescr2")}
        description3={t("mainBannerDescr3")}
        btnText={t("mainBannerBtn")}
        {...windowSize}
        setCurPage={setCurPage}
      />
      <ProsModule />
      <MinorHeader
        header={t("minorHeader1")}
        description1={t("minorHeader1Descr1")}
        description2={t("minorHeader1Descr2")}
        isXs={windowSize.isXs}
        isSm={windowSize.isSm}
      />
      <ProsModule2 />
      <MinorHeader
        header={t("minorHeader2")}
        description1={t("minorHeader2Descr1")}
      />

      <GuideModule />

      <MinorHeader
        header={t("minorHeader3")}
        description1={t("minorHeader3Descr1")}
      />
      <FDModule {...windowSize} />
      <div style={{ textAlign: "left" }}>
        <h4
          className={`${styles.minorHeader} mt-5 mb-0`}
          style={{ ...s.minorHeader, fontFamily: "Gilroy-semibold" }}
        >
          {t("qMinorHeader")}
        </h4>
      </div>

      <QuestionsModule />
    </>
  );
}
