      qDescr1:
        "Unfortunately, we cannot provide you with a test period. However, if for any reason the server is not suitable for you, we will gladly refund your money according to our service policy, except for promotional rates (DE-1, NL-1, FI-1 and EE-1), for which no refunds are available under any circumstances.",
      qDescr2:
        "A virtual server uses some of the resources of the physical server on which the virtual machine is running. You have full control over these resources, and they will not be used by other users of the physical server. A dedicated server, unlike a virtual server, uses 100% of the resources because it is a full physical machine.",
      qDescr3:
        "Our defense system integrates seamlessly with all cloud servers, with no additional configuration or fees required. It works continuously, effectively blocking attacks up to 2 Tbps. This system is suitable for any application, including game servers and high load internet projects. Trust us and you'll always be protected.",
      qDescr4:
        "All cloud servers are powered by KVM virtualization, which ensures that each server is completely isolated from the others. This gives you full access to the system core and firewall.",

